      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', `${url}js/jquery.countdown.js`)
      document.head.appendChild(recaptchaScript)
    },
    mounted() {
      const url = window.location.href
      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', `${url}js/jquery.countdown.js`)
      document.head.appendChild(recaptchaScript)
      // console.log('from main', store.get('test'))
      this.checkFormData()
      this.getStatsNIG()
      setInterval(() => {
        this.getStatsNIG()
      }, 10000)

    },
    methods: {
      setLoader(options = {}) {
        this.loader = {
          show: !options ? true : options.show
        }
      },
      postResult(resultData) {
        // console.log('result data', resultData)
        axios.post('https://main.jappsinc.com/api/result',resultData)
                .then(res => {
                  localStorage.removeItem('formData');
                  document.getElementById("questions").contentDocument.location.reload(true);
                  this.checkFormData();
                  return res
                })
      },
      checkFormData() {
        const myVar = setInterval(check, 1000);
        let _this = this;
        function check() {
          if (localStorage.formData) {
            console.log('gotten result')
            clearInterval(myVar);
            const data = JSON.parse(localStorage.formData)
            const steps = data[0].steps

            let question_1 = steps.filter(step => {
              return step.name === 'question_1[]'
            })

            let question_2 = steps.filter(step => {
              return step.name == 'question_2[]'
            })

            let question_3 = steps.filter(step => {
              return step.name == 'question_3[]'
            })

            let question_4 = steps.filter(step => {
              return step.name == 'question_4[]'
            })

            let question_5 = steps.filter(step => {
              return step.name == 'question_5[]'
            })

            let question_6 = steps.filter(step => {
              return step.name == 'question_6[]'
            })

            let question_7 = steps.filter(step => {
              return step.name == 'question_7[]'
            })

            let question_8 = steps.filter(step => {
              return step.name == 'question_8[]'
            })

            let dname = steps.filter(step => {
              return step.name == 'name[]'
            })

            let demail = steps.filter(step => {
              return step.name == 'email[]'
            })

            let dphone = steps.filter(step => {
              return step.name == 'phone[]'
            })

            let dage = steps.filter(step => {
              return step.name == 'age[]'
            })

            let dgender = steps.filter(step => {
              return step.name === 'gender[]'
            })

            const allData = {
              step_1: question_1,
              step_2: question_2,
              step_3: question_3,
              step_4: question_4,
              step_5: question_5,
              step_6: question_6,
              step_7: question_7,
              step_8: question_8,
              step_9: {
                name: dname[0].value,
                email: demail[0].value,
                phone: dphone[0].value,
                age: dage[0].value,
                gender: dgender[0].value
              }
            }
            _this.postResult(allData)
          }else {
            // console.log('checking...')
          }
        }

      },
      async getStatsNIG() {
        this.setLoader()
        await axios.get('https://main.jappsinc.com/api/reports/nigeria')
                .then(res => {
                  this.setLoader({show:false})
                  // console.log(res.data.data)
                  const dt = res.data.data.cases;
                  this.total_cases = dt.total_cases;
                  this.total_recovered = dt.total_recovered;
                  this.total_unresolved = dt.total_unresolved;
                  this.total_new_cases_today = dt.total_new_cases_today;
                  this.total_new_deaths_today = dt.total_deaths;
                  this.total_active_cases = dt.total_active_cases;
                  this.total_serious_cases = dt.total_serious_cases;
                  $("#getting-started").countdown("2017/10/24 15:30:30", function(e) {
                    $(this).html(e.strftime(`${dt.total_cases} Confirmed <br> <span class="time">${dt.total_active_cases} Active | ${dt.total_recovered} Discharged | ${dt.total_deaths} Death</span> <span class="text">... We can win this!</span>`))
                  })
                })
                .catch(err => {
                  console.log(err)
                  // alert('Sorry an error occurred please reload or try again later');
                  this.setLoader({show:false})
                  return err
                })
        // this.setCases()
      },
    }
  }
